import { IUserWord, IWord } from '../interfaces/IWord';

export default function shuffle(array: IWord[] | IUserWord[]) {
  const arr = [...array];
  for (let i = arr.length - 1; i > 0; i -= 1) {
    const j = Math.floor(Math.random() * (i + 1));
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}
